import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";
import "./Movie.css";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: [],
  };
  //   밑의 코딩중 'getMovies = () => {} ' 에서 'getMovies = async () => {}' async는 비동기라고 선언한것." 너는 이걸 기다려야되"
  //    'const movies = axios.get' 에서 ' const movies = await axios.get'의 await는
  //   "내가 뭘 기다리길 원해? " "axios를 기디리길 원해. axios가 끝날때까지 기다려!"
  getMovies = async () => {
    const {
      data: {
        data: { movies },
      },
    } = await axios.get(
      "https://yts-proxy.now.sh/list_movies.json?sort_by=rating"
    );
    this.setState({ movies, isLoading: false });
  };
  componentDidMount() {
    this.getMovies();
    // setTimeout(() => {
    //   this.setState({ isLoading: false, book: true });
    // }, 6000);
    // // 6초후에 false로 바꾼다
  }
  render() {
    const { isLoading, movies } = this.state;
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map((movie) => (
              <Movie
                key={movie.id}
                id={movie.id}
                year={movie.year}
                title={movie.title}
                summary={movie.summary}
                poster={movie.medium_cover_image}
                genres={movie.genres}
              />
            ))}
          </div>
        )}
      </section>
    );
  }
  //   true: Loading... false : We are ready
}

export default App;
